var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5"},[_vm._m(0),_vm._m(1),_vm._m(2),_vm._m(3),_c('br'),_c('br'),_vm._m(4),_vm._m(5),_vm._m(6),_c('div',{staticClass:"tarjeta color-primario p-3 mb-5"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(7),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(8),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-contenido texto-blanco",attrs:{"href":_vm.obtenerLink('/downloads/clasificacion_productos.pdf'),"target":"_blank"}},[_c('span',[_vm._v("Descargar")]),_c('i',{staticClass:"fas fa-file-download"})])])])])])]),_c('h3',{staticClass:"titulo-tercero"},[_vm._v("Atributos")]),_c('p',[_vm._v("Para Kotler (2017), el desarrollo de un producto o servicio implica definir los beneficios que ofrecerá. Estos beneficios se comunican y entregan a través de atributos del producto como calidad, características y estilo y diseño.")]),_vm._m(9),_c('div',{staticClass:"row justify-content-center mt-5 my-5"},[_vm._m(10),_vm._m(11),_c('P',{staticClass:"mt-5"},[_vm._v("Cada uno la percibe de forma distinta; para unos significa durabilidad, confiabilidad y para otros apariencia y facilidad de uso, entre otros aspectos. (Hernandez,2017) Tiene dos elementos:")])],1),_vm._m(12),_vm._m(13),_vm._m(14),_vm._m(15),_c('p',{staticClass:"mb-5"},[_vm._v("A continuación, Hernández (2017) realiza un cuadro sobre las características e implicaciones de cada etapa del ciclo de vida del producto.")]),_vm._m(16),_c('p'),_c('Separador'),_c('br'),_c('br'),_vm._m(17),_c('p',{staticClass:"mb-3"},[_vm._v("Un servicio es una forma de producto que consiste en actividades, beneficios o satisfacciones que se ofrecen en venta y siendo esencialmente intangibles, no tienen como resultado la propiedad de algo. Algunos ejemplos son los servicios bancarios, el hospedaje en hoteles, los vuelos en aerolíneas, el comercio minorista y la comunicación inalámbrica (Kotler,2017).")]),_c('p',[_vm._v("¿Cómo se diseña un concepto de servicio? Para Lovelock (2015) El mercadeo de servicios experimentados reconocen la necesidad de adoptar una perspectiva integral del desempeño que desean que sus clientes experimenten. La propuesta de valor debe considerar e integrar tres componentes:")]),_vm._m(18),_c('h3',{staticClass:"titulo-tercero"},[_vm._v("Experiencias")]),_c('p',{staticClass:"mb-3"},[_vm._v("Para diferenciar sus ofertas, más allá del simple hecho de fabricar productos y prestar servicios, las empresas están creando y entregando experiencias para los clientes con sus marcas o su compañía. Las experiencias siempre han formado parte importante del marketing. Desde hace mucho tiempo Disney crea sueños y recuerdos a través de sus películas y parques temáticos. Y hace mucho que Nike declaró: “No se trata tanto de los zapatos, sino de a dónde te llevan”. Hoy todo tipo de empresas están rehaciendo sus tradicionales bienes y servicios para crear experiencias (Kotler,2017).")]),_c('p',[_vm._v("Los consumidores valoran las experiencias y las soluciones deseadas, y están dispuestos a pagar por ellas. No se puede poseer seres humanos, aunque sí puede contratar (alquilar) su trabajo y experiencia. Al pagar por el uso temporal de un objeto o por el acceso a una instalación física, los clientes disfrutan del uso de cosas que no pueden comprar o justificar su compra, o bien, que prefieren no conservar ni almacenar después de su uso. Además, el arrendamiento -por medio de tarifas de acceso y uso- ofrece a los clientes un medio para participar en sistemas de redes que los individuos y la mayoría de las organizaciones no podrían costear ni operar por sí mismos (Lovelock,2015).")]),_vm._m(19),_c('h3',{staticClass:"titulo-tercero"},[_vm._v("Características")]),_c('p',{staticClass:"mb-5"},[_vm._v("Según Kotler (2017) se tienen en cuenta cuatro características especiales de los servicios al diseñar programas de marketing: intangibilidad, inseparabilidad, variabilidad y caducidad.")]),_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"b","clase-tarjeta":"tarjeta tarjeta--azul"}}),_vm._m(20),_vm._m(21),_vm._m(22),_vm._m(23),_vm._m(24),_vm._m(25),_c('Separador'),_c('br'),_c('br'),_vm._m(26),_vm._m(27),_c('p',{staticClass:"mt-5"},[_vm._v("La marca de un producto es aquello que lo identifica, que lo diferencia de otros similares, de manera que se convierte en el trasmisor de la esencia y los beneficios que aporta el producto, así como la proyección en el mercado de la imagen y el prestigio de la empresa que lo fabrica o lo comercializa (Martínez,2014).")]),_c('p',[_vm._v("Para Hernández (2017) “Los objetivos que se persiguen para otorgar una marca a un producto son: ser un signo de garantía para el producto, dar prestigio y seriedad a la empresa fabricante y ayudar a que se venda mejor el producto y las características primordiales de las marcas ponen énfasis en que su nombre no debe ser genérico; éste describe realmente al producto. El nombre debe ser corto, debe ser fácil de recordar, el sentido del nombre debe ser agradable, debe ser adaptable a cualquier medio de publicidad y se debe identificar fácilmente a simple vista.")]),_c('p',[_vm._v("Ricardo Hoyos (2016) define la importancia que tienen las marcas para el ser humano contemporáneo, como individuo y como ser social. A través de la marca, el ser humano se legitima frente a sí mismo y frente a los demás; por medio de ella se establecen afinidades con sus grupos de interés y distancias con personas y grupos sociales que considera diferentes. En razón de la marca, los individuos crean identidad de sí mismos y de sus grupos sociales.")]),_c('p',{staticClass:"mb-5"},[_vm._v("En el siguiente cuadro se muestran los beneficios de la marca para el consumidor")]),_vm._m(28),_c('p',{staticClass:"mb-5"},[_vm._v("La administración de la marca es la actividad en la que una organización emplea un nombre, frase, diseño o símbolos, o combinaciones de estos, para identificar sus productos y distinguirlos de los competidores. Un nombre de marca es cualquier palabra, elemento (diseño, sonido, forma o color) o combinación de estos, que se emplea para distinguir los bienes o servicios de un vendedor. El nombre comercial es una identidad mercantil y legal bajo la cual una empresa hace negocios. Una marca registrada identifica a una empresa registrada legalmente con su nombre de marca o con su nombre comercial, por lo que tiene la exclusividad de su uso, evitando que otros lo empleen. (Kerin,2018)")]),_c('h3',{staticClass:"titulo-tercero"},[_vm._v(" Tipos de marcas")]),_vm._m(29),_vm._m(30),_vm._m(31),_vm._m(32),_vm._m(33),_c('h3',{staticClass:"titulo-tercero"},[_vm._v(" Construcción de marcas fuertes")]),_vm._m(34),_vm._m(35),_vm._m(36),_vm._m(37),_vm._m(38),_vm._m(39),_c('img',{staticClass:"mt-5 img-a",attrs:{"src":require("@/assets/curso/img_22.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("Las marcas exitosas y establecidas asumen una personalidad de marca, que es un conjunto de características humanas asociadas con un nombre de marca. Los estudios demuestran que los consumidores suelen asignar rasgos de personalidad a los productos: tradicionales, románticos, resistentes, sofisticados, rebeldes, y que eligen las marcas que concuerdan más con la imagen propia o deseada. Se da personalidad a una marca mediante la publicidad que representa a cierto usuario o situación de uso y que transmite ciertas emociones o sentimientos que se asociaran con la marca. E. Harley-Davidson tiene rasgos asociados con masculinidad, desafío e individualismo vigoroso (Kerin,2018).")]),_vm._m(40),_c('img',{staticClass:"mt-5 img-a img-fluid",attrs:{"src":require("@/assets/curso/img_23.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("El branding o la creación de una marca es un proceso integrativo, que busca construir marcas poderosas; es decir, marcas ampliamente conocidas, asociadas a elementos positivos, deseadas y compradas por una base amplia de consumidores. Esto incluye la definición de la identidad o imagen que se quiere tener en un mercado, su diseño gráfico y la puesta en escena de la marca frente a determinados públicos, de manera que tome relevancia y, además, rentabilidad a través de todos los procesos de compra y recompra de la misma (Hoyos,2016).")]),_vm._m(41),_vm._m(42),_c('img',{staticClass:"mt-5 img-a img-fluid",attrs:{"src":require("@/assets/curso/img_27.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("El posicionamiento es lo que queremos que el consumidor piense acerca de nuestro producto, es decir, aquello que se constituye en la idea básica de venta, que generalmente es una afirmación o una frase. El posicionamiento se requiere cuando hay muchas alternativas de productos y marcas, muchos anuncios publicitarios, así como cuando la competencia ataca y cuando el producto entra al mercado. (Prieto,2013)")]),_c('p',[_vm._v("Para Prieto (2013) al hablar de posicionamiento se deben plantear las siguientes inquietudes: ")]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_vm._m(43),_vm._m(44),_vm._m(45),_vm._m(46),_c('p',[_vm._v("Para Ricardo Hoyos (2016) las principales variables para posicionar un producto son:")]),_vm._m(47),_vm._m(48),_c('Separador'),_c('br'),_c('br'),_vm._m(49),_c('img',{staticClass:"mt-5 img-a img-fluid",attrs:{"src":require("@/assets/curso/img_30.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("El envase junto con la etiqueta informa al posible comprador sobre el producto, que contiene, sus características, la empresa que lo elabora y la marca. (Martinez,2014)")]),_c('p',[_vm._v("Alicia Martínez García (2014) describe a continuación los tipos de envases, sus funciones y condiciones. ")]),_c('h2',{staticClass:"text-center mt-5"},[_vm._v("Tipos de envases.")]),_c('img',{staticClass:"mb-5 img-a i