reocupación por el medio ambiente"}},[_c('div',{staticClass:"col-lg-10"},[_c('p',[_vm._v("Debido a la inquietud generalizada en todo el mundo por el aumento de los desechos sólidos y por el déficit de rellenos sanitarios adecuados, la cantidad, composición y eliminación de los materiales de empaquetado continúan recibiendo mucha atención con la finalidad de reducir los desechos sólidos. (Kerin,2018)")])])])]),_c('Separador'),_c('br'),_c('br'),_vm._m(54),_vm._m(55),_c('div',{staticClass:"row justify-content-center align-items-center mt-5"},[_vm._m(56),_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"card r30 p-3 tarjeta--gris mb-5"},[_c('div',{staticClass:"card-body"},[_c('div',{staticClass:"h3"},[_vm._v("Función de protección")]),_c('p',{staticClass:"mb-3"},[_vm._v(" La protección que da el embalaje al producto es de vital importancia para llevar a efecto su distribución masiva. Los materiales del envase y el embalaje que se utilizan comúnmente son:")]),_vm._m(57),_vm._m(58),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"tarjeta color-secundario p-3 mb-5"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(59),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-contenido texto-blanco",attrs:{"href":_vm.obtenerLink('/downloads/Tipos de materiales_INFOGRAFIA.pdf'),"target":"_blank"}},[_c('span',[_vm._v("Descargar")]),_c('i',{staticClass:"fas fa-file-download"})])])])])])])])])])])])]),_c('Separador'),_c('br'),_c('br'),_vm._m(60),_c('img',{staticClass:"mt-5 img-a img-fluid",attrs:{"src":require("@/assets/curso/img_35.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("Una etiqueta es una parte integral del empaque, la cual identifica el producto o marca, quién, dónde y cuándo lo hizo, cómo debe usarse, cuál es su contenido y sus ingredientes (Kerin,2018).")]),_c('p',{staticClass:"mb-5"},[_vm._v("Hernández (2017), explica la etiqueta como “la parte del producto que contiene la información escrita sobre el artículo. Una etiqueta puede ser parte del embalaje o simplemente una hoja adherida directamente al producto. La etiqueta se utiliza para facilitar la identificación de un producto al presentar la marca y un diseño gráfico único”. A continuación, describe los objetivos, elementos y características que deben tener las etiquetas. ")]),_c('h3',{staticClass:"titulo-tercero"},[_vm._v(" Objetivos de las etiquetas")]),_vm._m(61),_c('h3',{staticClass:"titulo-tercero"},[_vm._v(" Elementos que debe considerar la etiqueta")]),_c('img',{staticClass:"img-a img-fluid",attrs:{"src":require("@/assets/curso/img_36.png")}}),_vm._m(62),_vm._m(63),_c('h3',{staticClass:"titulo-tercero mt-5",attrs:{"id":"tema1_7_1"}},[_vm._v("Códigos barras")]),_c('img',{staticClass:"img-a img-fluid",attrs:{"src":require("@/assets/curso/img_42.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("Para la identificación del producto se ha extendido el uso del código de barras que se abrevia en inglés como UPC y significa Código Universal del Producto. Es indispensable para aquellos bienes que se adquieren en el autoservicio o almacenes, a efecto de que las computadoras de una bodega o un comercio identifiquen plenamente cada artículo a través de la lectura con rayo láser y se activen los programas de precios y facturación, así como las entradas y salidas del inventario (Hernández,2017).")]),_c('p',[_vm._v("En el sector del comercio, sobre todo, pero también en muchos otros sectores, el código de barras responde a dos necesidades: la primera, tener una codificación de cobertura universal, de modo que un ítem tenga un código que lo identifique en todo el mundo y que sea irrepetible; la segunda, que el código sea leído por una máquina, que se elimine la digitación, para efectos de eliminación de errores y de rapidez de operación. El lenguaje de barras paralelas verticales, de diferente grosor y diferente espesor de separación, con el que se crean códigos alfanuméricos, posibles de leer mediante un rayo láser y ser transmitidos a un sistema de computación (Vélez,2014).")]),_c('p',{staticClass:"mb-5"},[_vm._v("Actualmente, el Código EAN 13. Es el más utilizado entre todas las aplicaciones del GS1. A veces conocido como GTIN (Global Trade Item Number). Es el código de uso corriente para lectura en las cajas registradoras en los autoservicios, es decir, es el sistema utilizado para la codificación de bienes de consumo. El EAN 13 está compuesto por 13 dígitos, generalmente la estructura de la codificación es la siguiente según Vélez (2014):")]),_vm._m(64),_vm._m(65),_vm._m(66),_c('img',{staticClass:"img-a img-fluid",attrs:{"src":require("@/assets/curso/img_45.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("La tecnología para identificar objetos mediante ondas de radiofrecuencia llamada Radio Frequency Identification (RFID), también denominada Dedicated Short Range Communication (DSRC). Esta tecnología identifica el producto y sigue su ruta desde la fábrica hasta el consumidor. Tiene muchas ventajas: no es necesario que esté en contacto directo con un escáner —ya que permite identificar y localizar al proveedor de artículos más inmediato en la cadena de suministro de cualquier compañía, sector y ubicación en el mundo—; realiza lecturas más rápidas y más precisas (eliminando la necesidad de tener una línea de visión directa); permite niveles más bajos en el inventario; mejora el flujo de caja y la reducción potencial de gastos generales; reduce mermas de inventario, capacidad de informar al personal o a los encargados del tiempo para reabastecer las estanterías o corregir la ubicación del artículo que se ha colocado en el sitio equivocado; ayuda a conocer exactamente qué elementos han sido sustraídos y de ser necesario, dónde localizarlos; previene el robo en tienda; lucha contra la falsificación y facilita el retiro del mercado del producto que ha caducado (Hernandez,2017).")]),_c('p',[_vm._v("La etiqueta RFID se trata del código EPC (Electronic Product Code) que se encuentra almacenado en un circuito integrado, llamado tag y que se transmite y se lee por radiofrecuencia en el sistema RFID. El EPC está administrado en el mundo por la organización EPC Global, filial de GS1. Su formato está conformado mediante 24 posiciones (alfanuméricas), contra los 13 dígitos del código GTIN del EAN-13, distribuidos en cuatro secciones (Vélez, 2014).")]),_c('p',[_vm._v("A continuación, Vélez (2014) nos describe las posiciones de la etiqueta RFID.")]),_vm._m(67),_c('p',[_vm._v("El sistema opera mediante una tarjeta o tag, que contiene un chip con una memoria interna, variable hasta millares de bytes, según el modelo y una antena transmisora, adherida al ítem, que puede ser objeto, animal o aún, persona y que, mediante ondas de radio, entrega los datos de un código a un lector RFID que los capta y puede transmitirlos a un sistema de computación, para ser procesados. Indiscutiblemente el código de barras será sustituido rápidamente por aplicaciones y utilizaciones prácticas y rutinarias del sistema RFID (Vélez, 2014).")]),_c('Separador'),_c('br'),_c('br'),_vm._m(68),_c('img',{staticClass:"img-a img-fluid",attrs:{"src":require("@/assets/curso/img_47.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("Kotler (2017), determina que tanto los consumidores como las empresas están cambiando su perspectiva sobre los problemas ambientales y da las siguientes perspectivas:")]),_vm._m(69),_c('Separador'),_c('br'),_c('br'),_vm._m(70),_c('img',{staticClass:"img-a img-fluid",attrs:{"src":require("@/assets/curso/img_48.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("El marketing social se aplica cuando existen organizaciones que, comprometidas con la situación de una ciudad, desarrollan programas de comunicación, buscando concientizar a los ciudadanos acerca de la necesidad de mantener en buen estado los parques, no fumar en lugares públicos, respetar a los ancianos, navidad sin pólvora o sin niños quemados, entre otras. En general el marketing social pretende generar, a través de sus acciones, protección y bienestar para la comunidad (Prettel,2016). ")]),_vm._m(71),_c('p',[_vm._v("Para Forero (2017), el marketing social hace referencia a las consideraciones relacionadas con la responsabilidad social, el medio ambiente y el respeto de los valores de las personas y de las comunidades. En esta nueva concepción, el marketing ha pasado de ser útil para la promoción de productos para convertirse en un instrumento vital para promover inclusive ideas y servicios (Nieto 1988).")]),_c('p',[_vm._v("Según Stanton, (2007) la responsabilidad social de una empresa se extiende las dimensiones de amplitud y compromiso de sus metas de marketing lo suficiente para cumplir su responsabilidad social, pone en práctica lo que se conoce como el concepto de marketing social.")]),_vm._m(72),_c('p',{staticClass:"mb-5"},[_vm._v("El marketing social consiste en motivar comportamientos que generen bienestar individual y social. Los programas de marketing social abarcan un amplio rango de asuntos. Se desarrollan campañas de propaganda social que hablan de asuntos como salud preventiva, educación, sustentabilidad ambiental, derechos humanos y seguridad personal. En todo caso, el marketing social implica mucho más que difundir campañas de información; implica una amplia variedad de estrategias y herramientas de mezcla de marketing diseñadas para motivar cambios sociales benéficos (Kotler,2017).")]),_c('Separador'),_c('br'),_c('br'),_vm._m(73),_vm._m(74),_vm._m(75),_c('p',[_vm._v("Para la protección de los derechos de autor esta la dirección nacional de derechos de autor http://derechodeautor.gov.co/ Ente institucional para fortalecer la debida y adecuada protección de los diversos titulares del derecho de autor y los derechos conexos, contribuyendo a la formación, desarrollo y sustentación de una cultura nacional de